body {
  margin: 0;
  padding: 0;
  font-family: monospace;
  padding-top: 1%;
}

img {
  width: 100px;
  margin-top: 1.5%;
  display: block;
  margin: 0 auto;
}

h2{
  margin: 0 auto;
  text-align: center;
  margin-top: 1%;
  margin-bottom: 1%;
}

section {
  display: block;
  height: 5vh;
  overflow: auto;
  background-color: pink;
  color: red;
  font-size: 1.4em;
  padding: 0.5%;
}

.container {
  height: 75vh;
  display: -ms-flexbox;
  display: flex;
}

pre, textarea {
  width: 50%;
  font-family: monospace;
  font-size: 1.3em;
  padding: 0.5%;  
  overflow: auto;
}

pre {
  background-color: #f8f8f8;
  margin-top: 0;
  color: seagreen;
}

textarea:focus {
  outline: 0;
  color: purple;
}


